import React, { Component } from 'react';
import {Link } from 'react-router-dom'
import '../App.css';

class Home extends Component {
	constructor(props) {
		super(props)
		window.level = 1
		this.state = {
			level: window.level,
		}
	}
	setLevel(i) {
		window.level=i
		this.setState({level: i})
	}
	render() {
		let levels = []
		for (let i=1; i<6; i++) {
			let className = "Home-button"
			if (i===this.state.level) {
				className += " Home-button-hi"
			}
			levels.push(
				<div key={i} className={className} onClick={(e) => this.setLevel(i) }>
					{i}级
				</div>
				)
		}
		let exercises = [
			// ['note',"单音 Note"],
			['interval',"音程 Interval"],
			['chord',"和弦 Chord"],
			['keysignature',"调号 Key Signature"],
			['scale',"音阶 Scale"],
		]

		return (<div style={{padding: 10}}>
			<div style={{display: 'table'}}>
				<h3>单项练习</h3>
				{
					exercises.map((item) => {
						return(
							<Link key={item[0]} to={'/exercise/'+item[0]}>
								<div className='Home-icon'>
									<p>{item[1]}</p>
								</div>
							</Link>
							)
					})
				}
			</div>
			
			<h3>测试 - {this.state.level}级</h3>
			<div style={{display: 'table'}}>
				{levels}
			</div>
			<div style={{display: 'table'}}>
				<Link to='/exam/interval'>
					<div className='Home-icon'>
						<p>音程测试</p>
					</div>
				</Link>
				<Link to='/exam/chord'>
					<div className='Home-icon'>
						<p>和弦测试</p>
					</div>
				</Link>
				<Link to='/exam/scale'>
					<div className='Home-icon'>
						<p>调式音阶测试</p>
					</div>
				</Link>
				<Link to='/exam'>
					<div className='Home-icon'>
						<p>综合测试音程、和弦、调式等</p>
					</div>
				</Link>
			</div>
			<div>
				<h3>工具</h3>
				<Link to='/editor'>
					<div className='Home-icon'>
						<p>自由打谱</p>
					</div>
				</Link>
			</div>
		</div>)
	
	}
}

export default Home;



